// src/components/OrderCard.js
import React, { useState } from 'react';
import { generatePDF } from '../utils/generatePDF';

const OrderCard = ({ order, index, onUpdate, onDelete }) => {
  const [editing, setEditing] = useState(false);
  const [updated, setUpdated] = useState(order);
  const [showModal, setShowModal] = useState(false);
  const [deleteCode, setDeleteCode] = useState('');
  const [error, setError] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUpdated({ ...updated, [name]: value });
  };

  const handleEquipmentChange = (i, field, value) => {
    const updatedEquipment = [...updated.equipment];
    updatedEquipment[i][field] = value;
    setUpdated({ ...updated, equipment: updatedEquipment });
  };

  const handleSave = () => {
    onUpdate(index, updated);
    setEditing(false);
  };

  const handleConfirmDelete = () => {
    if (deleteCode === '0000') {
      onDelete(index);
      setShowModal(false);
      setDeleteCode('');
      setError('');
    } else {
      setError('–ù–µ–≤–µ—Ä–Ω—ã–π –∫–æ–¥. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ —Å–Ω–æ–≤–∞.');
    }
  };

  return (
    <div className="card mb-3 shadow-sm">
      <div className="card-body">
        {editing ? (
          <>
            <h6>–û–±—â–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è</h6>
            <label className="form-label">–ó–∞–º–µ—Ç–∫–∏</label>
            <textarea
              className="form-control mb-3"
              name="notes"
              value={updated.notes}
              onChange={handleChange}
            />

            <h6 className="mt-3">–û–±–æ—Ä—É–¥–æ–≤–∞–Ω–∏–µ</h6>
            {updated.equipment.map((eq, i) => (
              <div key={i} className="border rounded p-3 mb-3">
                <p><b>{eq.customType || eq.type}</b> ‚Äì {eq.name} ‚Äì SN: {eq.serial}</p>

                <label className="form-label">–°—Ç–∞—Ç—É—Å —Ä–µ–º–æ–Ω—Ç–∞</label>
                <select
                  className="form-select mb-2"
                  value={eq.status || '–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞'}
                  onChange={(e) => handleEquipmentChange(i, 'status', e.target.value)}
                >
                  <option value="–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞">–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞</option>
                  <option value="–†–µ–º–æ–Ω—Ç">–†–µ–º–æ–Ω—Ç</option>
                  <option value="–ì–æ—Ç–æ–≤–æ">–ì–æ—Ç–æ–≤–æ</option>
                </select>

                {eq.status === '–ì–æ—Ç–æ–≤–æ' && (
                  <>
                    <label className="form-label">–ü—Ä–æ–≤–µ–¥—ë–Ω–Ω—ã–µ —É—Å–ª—É–≥–∏</label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      placeholder="–ù–∞–ø—Ä–∏–º–µ—Ä: –ó–∞–º–µ–Ω–∞ –ø–ª–∞—Ç—ã"
                      value={eq.services || ''}
                      onChange={(e) => handleEquipmentChange(i, 'services', e.target.value)}
                    />

                    <label className="form-label">–°—Ç–æ–∏–º–æ—Å—Ç—å —Ä–µ–º–æ–Ω—Ç–∞</label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      placeholder="–°—É–º–º–∞"
                      value={eq.price || ''}
                      onChange={(e) => handleEquipmentChange(i, 'price', e.target.value)}
                    />

                    <div className="form-check mb-2">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        checked={eq.guarantee || false}
                        onChange={(e) => handleEquipmentChange(i, 'guarantee', e.target.checked)}
                      />
                      <label className="form-check-label">–ì–∞—Ä–∞–Ω—Ç–∏–π–Ω—ã–π —Ä–µ–º–æ–Ω—Ç</label>
                    </div>
                  </>
                )}
              </div>
            ))}

            <button className="btn btn-success btn-sm me-2" onClick={handleSave}>
              –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
            </button>
            <button className="btn btn-secondary btn-sm" onClick={() => setEditing(false)}>
              –û—Ç–º–µ–Ω–∞
            </button>
          </>
        ) : (
          <>
            <p><b>–ö–ª–∏–µ–Ω—Ç:</b> {order.clientName} ({order.phone})</p>
            <p><b>–ì–æ—Ä–æ–¥:</b> {order.city}</p>
            <p><b>–î–∞—Ç–∞:</b> {order.date}</p>
            <p><b>–¢–µ—Ö–Ω–∏–∫:</b> {order.technician}</p>
            <p><b>–ó–∞–º–µ—Ç–∫–∏:</b> {order.notes}</p>

            <b>–û–±–æ—Ä—É–¥–æ–≤–∞–Ω–∏–µ:</b>
            <ul>
              {order.equipment.map((eq, i) => (
                <li key={i}>
                  <div><b>{eq.customType || eq.type}</b> ‚Äì {eq.name} ‚Äì SN: {eq.serial}</div>
                  <div><small>–°—Ç–∞—Ç—É—Å: {eq.status || '–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞'}</small></div>
                  {eq.services && <div><small>–£—Å–ª—É–≥–∏: {eq.services}</small></div>}
                  {eq.price && <div><small>–°—Ç–æ–∏–º–æ—Å—Ç—å: {eq.price} —Ç–≥</small></div>}
                  {eq.guarantee && <div><small>–ì–∞—Ä–∞–Ω—Ç–∏—è: –î–∞</small></div>}
                </li>
              ))}
            </ul>

            <button className="btn btn-outline-primary btn-sm me-2" onClick={() => setEditing(true)}>
              –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å
            </button>
            <button className="btn btn-outline-success btn-sm me-2" onClick={() => generatePDF(order)}>
              –°–∫–∞—á–∞—Ç—å –ê–í–†
            </button>
            <button className="btn btn-outline-danger btn-sm" onClick={() => setShowModal(true)}>
              –£–¥–∞–ª–∏—Ç—å
            </button>
          </>
        )}
      </div>

      {/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —É–¥–∞–ª–µ–Ω–∏—è */}
      {showModal && (
        <div className="modal show fade d-block" tabIndex="-1" role="dialog" style={{ backgroundColor: 'rgba(0,0,0,0.5)' }}>
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">–ü–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏–µ —É–¥–∞–ª–µ–Ω–∏—è</h5>
                <button type="button" className="btn-close" onClick={() => setShowModal(false)}></button>
              </div>
              <div className="modal-body">
                <p>–í–≤–µ–¥–∏—Ç–µ –∫–æ–¥ –¥–ª—è –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è –∑–∞—è–≤–∫–∏:</p>
                <input
                  type="password"
                  className="form-control"
                  value={deleteCode}
                  onChange={(e) => setDeleteCode(e.target.value)}
                  placeholder="–í–≤–µ–¥–∏—Ç–µ –∫–æ–¥"
                />
                {error && <div className="text-danger mt-2">{error}</div>}
              </div>
              <div className="modal-footer">
                <button className="btn btn-danger" onClick={handleConfirmDelete}>
                  –£–¥–∞–ª–∏—Ç—å
                </button>
                <button className="btn btn-secondary" onClick={() => {
                  setShowModal(false);
                  setDeleteCode('');
                  setError('');
                }}>
                  –û—Ç–º–µ–Ω–∞
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default OrderCard;
