// src/utils/generatePDF.js
import jsPDF from 'jspdf';
import 'jspdf-autotable'; 

export function generatePDF(order) {
  const doc = new jsPDF();

  doc.setFontSize(16);
  doc.text('–ê–∫—Ç –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã—Ö —Ä–∞–±–æ—Ç', 14, 20);

  doc.setFontSize(12);
  doc.text(`–ö–ª–∏–µ–Ω—Ç: ${order.clientName || '-'}`, 14, 30);
  doc.text(`–¢–µ–ª–µ—Ñ–æ–Ω: ${order.phone || '-'}`, 14, 36);
  doc.text(`–ì–æ—Ä–æ–¥: ${order.city || '-'}`, 14, 42);
  doc.text(`–î–∞—Ç–∞: ${order.date || '-'}`, 14, 48);
  doc.text(`–¢–µ—Ö–Ω–∏–∫: ${order.technician || '-'}`, 14, 54);
  doc.text(`–°—Ç–∞—Ç—É—Å: ${order.status || '-'}`, 14, 60);

  const equipmentData = order.equipment?.map((item, index) => [
    index + 1,
    item.type || '',
    item.name || '',
    item.serial || '',
  ]) || [];

  if (equipmentData.length > 0) {
    doc.autoTable({
      head: [['#', '–¢–∏–ø', '–ù–∞–∑–≤–∞–Ω–∏–µ', '–°–µ—Ä–∏–π–Ω—ã–π –Ω–æ–º–µ—Ä']],
      body: equipmentData,
      startY: 70,
    });
  }

  const notesY = (doc.autoTable?.previous?.finalY || 80) + 10;
  doc.text(`–ó–∞–º–µ—Ç–∫–∏: ${order.notes || '-'}`, 14, notesY);

  doc.save(`–ê–í–†_${order.clientName || 'client'}.pdf`);
}
